/* This css file is linked with 3 component :(Main porpose is to enable animation and media query task)
 1. sidebar ,
 2. home page 


/* ////////for home page */

.homecontent {
  position: static;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* background-color: #4b658723; */
  justify-content: center;
  border-radius: 20px;
  margin-bottom: 10px;
}

/* css that relates between home page and sidebar */
.side-bar {
  position: fixed;
  display: flex;
  right: 0px;
  width: "300px";
  background-color: #4b65879f;
  height: 550px;
  border-top: solid 5px rgb(37, 55, 71);
  border-left-color: rgb(100, 0, 55);
  border-right-color: rgb(80, 75, 7);
  top: 2%;
  bottom: 10%;

  border-top-right-radius: 1000px;
  border-bottom-left-radius: 700px;
  animation-name: sidebarAnimation;
  animation-duration: 8s;
  animation-iteration-count: 3;
  align-items: flex-start;
  flex-direction: column;
}

.sidebar-hide-mood {
  visibility: hidden;
}

/* \\small screen */

@media screen and (min-width: 599px) {
  .side-bar {
    position: fixed;
    display: flex;
    right: 0px;
    width: 350px;
    background-color: #4b65879f;
    height: 550px;
    border-top: solid 5px;
    border-top: solid 5px rgb(37, 55, 71);
    border-left-color: rgb(100, 0, 55);
    border-right-color: rgb(80, 75, 7);
    top: 2%;
    border-top-right-radius: 1000px;
    border-bottom-left-radius: 700px;
    border-top-left-radius: 10px;

    animation-name: sidebarAnimation;
    animation-duration: 8s;
    animation-iteration-count: 3;
  }
}

/* midium screen */

@media screen and (min-width: 600px) and (max-width: 1199px) {
  .side-bar {
    position: fixed;
    display: flex;
    right: 0px;
    width: 300px;
    background-color: #4b65879f;
    height: 670px;
    border-top: solid 5px;
    border-top: solid 5px rgb(37, 55, 71);
    border-left-color: rgb(100, 0, 55);

    top: 2%;
    border-top-right-radius: 1000px;

    border-bottom-left-radius: 700px;
    border-top-left-radius: 10px;
    animation-name: sidebarAnimation;
    animation-duration: 8s;
    animation-iteration-count: 3;
  }
}

/* large screen */
@media screen and (min-width: 1200px) {
  .side-bar {
    position: fixed;
    display: flex;
    right: 3%;
    width: 300px;
    background-color: #4b65879f;
    height: 700px;
    border-top: solid 10px rgb(37, 55, 71);
    border-left-color: rgb(100, 0, 55);

    top: 2%;
    border-top-right-radius: 1000px;

    border-bottom-left-radius: 700px;

    animation-name: sidebarAnimation;
    animation-duration: 8s;
    animation-iteration-count: 3;
  }
  .homecontent {
    position: static;
    /* margin-top: 120px; */
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /* background-color: #f7f6f27a; */
    justify-content: center;
    border-radius: 20px;
    margin-bottom: 10px;
    margin-left: 15%;
    margin-right: 15%;
  }
}

@keyframes sidebarAnimation {
  0% {
    background-color: #4b65879f;
  }
  25% {
    background-color: #4b65879f;
  }
  50% {
    background-color: #4667a55b;
  }
  100% {
    background-color: #113cc75e;
  }
}

